import * as React from 'react';
import { Tag } from "antd";

interface IInformTypeBoxProps {
    type: number
}

export class InformTypeBox extends React.Component<IInformTypeBoxProps> {

    public render() {
        const Boxes = [{
            color: "#607d8b",
            content: "宠物评价"
        }, {
            color: "#ff5516",
            content: "宠物评价回复"
        }, {
            color: "#f50",
            content: "宠都圈子评价"
        },{
            color: "#009688",
            content: "宠都圈子回复"
        },{
            color: "#ffc107",
            content: "宠都圈子"
        },]
        return (
            <div>
                <Tag color={Boxes[this.props.type].color}>{Boxes[this.props.type].content}</Tag>
            </div>
        );
    }
}
