import * as React from 'react';
import { Tag } from "antd";

interface IInformRelateTypeBoxProps {
    type: number
}

export class InformRelateTypeBox extends React.Component<IInformRelateTypeBoxProps> {

    public render() {
        const Boxes = [{
            color: "#673ab7",
            content: "不友善内容"
        }, {
            color: "#ff5516",
            content: "违法违规"
        }, {
            color: "#f50",
            content: "其他"
        },]
        return (
            <div>
                <Tag color={Boxes[this.props.type].color}>{Boxes[this.props.type].content}</Tag>
            </div>
        );
    }
}
